import React, { useEffect, useRef, useState } from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Dialog from "@mui/material/Dialog";
import { EventAPI } from "../../../commons/EventAPI";

const dispatchedEvents = [];

const SrsMuiDialog = function (props, ref) {

  const [open, setOpen] = useState(props.open || true);
  const innerRef = useRef(null);

  const closeDialog = () => {
    setOpen(false);
    props.dispatchEvent(EventAPI.DIALOG_CLOSED)
  }

  useEffect(() => {
    if(innerRef.current) {
      innerRef.current.addEventListener("DIALOG_CLOSED", closeDialog);
    }
    return () => {
      innerRef.current.removeEventListener("DIALOG_CLOSED", closeDialog);
    }
  }, [innerRef]);

  useEffect(() => {
    setOpen(props.open);
  }, [props.open]);


  return (
    <div ref={innerRef}>
      <Dialog {...props} open={open} theme={props.muiTheme}>
        <slot name="close_dialog"/>
        <slot />
      </Dialog>
    </div>
  );
};

const styles = `
  /* Custom styles for the MuiDialog component */
`;

const props = [
  {
    name: "open",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "disableEscapeKeyDown",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "maxWidth",
    defaultValue: "sm",
    type: "string",
  },
  {
    name: "fullWidth",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "scroll",
    defaultValue: "paper",
    type: "string",
  },
  {
    name: "aria-labelledby",
    defaultValue: null,
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to Dialog if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiDialog),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
